import wordMapping from "./data/word_to_code";

const keyMapping = {
  A: "日",
  B: "月",
  C: "金",
  D: "木",
  E: "水",
  F: "火",
  G: "土",
  H: "竹",
  I: "戈",
  J: "十",
  K: "大",
  L: "中",
  M: "一",
  N: "弓",
  O: "人",
  P: "心",
  Q: "手",
  R: "口",
  S: "尸",
  T: "廿",
  U: "山",
  V: "女",
  W: "田",
  X: "重",
  Y: "卜",
  Z: "Z",
};

function decompose(sentence) {
  return sentence.split("").map((word) => {
    let codes = wordMapping[word];
    let keys = null;
    if (codes !== undefined) {
      codes = codes.split("").map((char) => char.toUpperCase());
      keys = codes_to_key(codes);
    }
    return { word, codes, keys };
  });
}

function codes_to_key(codes) {
  return codes.map((code) => keyMapping[code]);
}

export { keyMapping, decompose };
